import React from 'react';
import { BseIconSelect } from './BaseIconSelect';
import { IIconSelectProps } from './interface';
import { useToolbarItemConfig } from '../common/hooks/useToolbarItemConfig';

export type { IOption as IconSelectOption } from './interface'; 
export type { IIconSelectProps as ITBIconSelectProps };
export const TBIconSelect = React.memo((props: IIconSelectProps) => {

  const { itemSize } = useToolbarItemConfig();

  return (itemSize === 'large') ? null : (<BseIconSelect size={itemSize} {...props} />)
});

TBIconSelect.displayName = 'iconSelect';